.header {
  width: 100%;
  height: 8rem;
  display: flex;
  padding: 0 10%;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to right, #171820, #3a3b4f);
}

.logo {
  font-size: 5rem;
  color: #0c0c10;
}

.nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav li {
  margin-left: 1.5rem;
  font-size: 2rem;
	font-weight: 600;
	letter-spacing: .3rem;
}

.nav a:link,
.nav a:visited {
  text-decoration: none;
  color: #0c0c10;
}

.nav a:hover,
.nav a.active {
  color: #9d9da7;
}
